@import url(https://fonts.googleapis.com/css2?family=Spectral:wght@300&display=swap);
@font-face {
    font-family: "Aristotelica";
    src: local("Aristotelica"),
     url(/zelasprolac/static/media/Aristotelica-Regular.9cc30fa9.ttf) format("truetype");
    font-weight: normal;
    }

    @font-face {
        font-family: "coolvetica";
        src: local("coolvetica"),
         url(/zelasprolac/static/media/coolvetica.857acc35.ttf) format("truetype");
        font-weight: normal;
        }
    


*{
    font-family: 'coolvetica', 'Spectral', serif;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    font-weight: normal;
}

.App{
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0%;
}

body::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #ededed;
  }
      
  body::-webkit-scrollbar {
    width: 8px;
    background-color: #ededed;
  }
      
  body::-webkit-scrollbar-thumb {
 
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
    background-color: #293591;
  }


  body{
    scrollbar-width: thin;
    scrollbar-color: #293591 #ededed;
    scrollbar-arrow-color: #ededed;
}


.h2-Style{
    font-size: 20px;
}

p{
    font-size: 14px;
}

h3{
    font-size: 20px;
}

.pageSection{
    width: 100%;
    overflow-x: hidden;
}

@media (min-width:768px){
    .h2-Style{
        font-size: 24px;
    }

    p{
        font-size: 16px;
    }
    
    h3{
        font-size: 22px;
    }
}

@media (min-width:1024px){
    .h2-Style{
        font-size: 30px;
    }

    p{
        font-size: 20px;
    }
    
    h3{
        font-size: 26px;
    }
}

@media (min-width:1440px){
    .h2-Style{
        font-size: 32px;
    }

    p{
        font-size: 22px;
    }
    
