    }
}

@media (min-width:1920px){
    .pageSection{
        margin: 0 auto;
        width: 1440px;
    }
}
.navbar{
    top: 0;
    width: 100%;
    height: 140px;
    background-color: #fff;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    z-index: 9;
}

.logo--navbar{
    display: flex;
    height: 138px;
    

}

.logo--navbar img{
    height: 100%;
    
}
.links-navBar{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
}

.links-navBar span{
    margin-right: 18px;
    font-weight: normal;
    cursor: pointer;
    color: #293591;
    display: none;
    font-size: 18px;
}

.navBox{
    width: 70px;
    height: 60px;
    background-color: #293591;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;

    justify-content: center;
    align-items: center;
}

.sideBar{
    width: 50vw;
    height: 100vh;
    z-index: 11;
    top: 0;
    
    position: fixed;
    transition: all ease-in-out 1s;

    background-color: #fff;

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

}

.header-siderbar{
    width: 100%;
    height: 100px;
    align-items: center;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-end;
    box-sizing: border-box;
    padding: 0 20px;
    background-color: #293591;
}

.sideBar-Items{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    
    width: 100%;
}



.sideBar-Items span{
    width: 100%;
    border-bottom: 1px solid #293591;
    margin-bottom: 10px;
    box-sizing: border-box;
    color: #293591;
    padding: 0 5px;

    
}

.sideBar-Items span:first-child{
    margin-top: 10px;
}

.opacity--sideBar{
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: 10;
    background-color: rgba(0, 0, 0, 0.24);
}

.flagsL{
    width: 25px;
    margin-right: 18px;
    cursor: pointer;
}

.select-idioma{
    margin-right: 10px;
    box-sizing: border-box;
    padding: 10px;
    
    border: none;
    border-bottom: 1px solid #000;
    color: #000;
    font-size: 18px;
    border: none;
    outline:none;
}



.select-idiomal:focus {
    outline: none !important;
    box-shadow: none !important;
}


.myselect {
    margin-right: 10px;
    position: relative;
    display: inline-block;
  }
  .myselect select {
    height: 30px;
    width: 80px;
    font-size: 18px;
    border:none;
    padding-right: 10px;
    outline: none;
    color: #293591; /* Firefox */
    -webkit-appearance:none; /* Safari and Chrome */
    appearance:none;
  }
  .myselect::after {
    content: '';
    position: absolute;
    right: 4px;
    top: 11px;
    width: 0; 
    height: 0; 
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-top: 6px solid #293591;
    pointer-events: none;
  }


@media (min-width:1024px){

    .navBox svg{
        display: none;
    }

    .links-navBar span{

        display: flex;
    }
}
.slider-container{
    position: relative;
    margin-top: 140px;
    width: 100%;
    height: 200px;
    
}

.slider-img{
    position: absolute;

    width: 100%;
    height: 100%;
    overflow: hidden;
    transition: all 1s ease-in-out;
}

.slider-img img{
    width: 100%;
    height: 100%;
}

.sliderSelector{
    position: absolute;
    bottom: 40px;
    left: 0%;
    width: 100%;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
}


.sliderItem{
    font-size: 16px;
    color: #fff;
    margin: 0 12px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.sliderItem > div{

    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #0e4f9d;
    cursor: pointer;
}

.text-slider{
    
    position: absolute;
    font-weight: bold;
    color: #0e4f9d;
    font-size: 18px;
    transition: opacity 1s ease-in-out;
}

.text-slider span{
    

    font-weight: bold;
}



@media (min-width:768px){
    .slider-container{
        height: 300px;
        
    }

    .text-slider{
        font-size: 22px;

    }
}

@media (min-width:1024px){
    .slider-container{
        height: 400px;
        
    }

    .text-slider{
        font-size: 30px;
    }
}

@media (min-width:1440px){
    .slider-container{
        height: 550px;
        
    }

    .text-slider{
        font-size: 38px;
    }
}

@media (min-width:1920px){
    .slider-container{
        height: 600px;
        
    }

    .text-slider{
        font-size: 38px;
    }
}


